import * as React from "react";
import { hot } from "react-hot-loader";
import { withRouter, Redirect, Link } from "react-router-dom";

import "./ChatBotView.scss";
import { Image, Input, Spin, Alert, Col, Row, Typography, Space } from "antd";
import { useContext, useState } from "react";
//import * as zipcodes from "zipcodes";

import { analytics, analyticsWrapper } from "../../../amplitude";

//https://lucasbassetti.com.br/react-simple-chatbot/#/docs/installation
import ChatBot from "react-simple-chatbot";

// eslint-disable-next-line @typescript-eslint/ban-ts-comment
// @ts-ignore
import MapExample from "./imgs/map-example.png";

import styled from "styled-components";

const Header = styled.div`
  align-items: center;
  background: #6e48aa;
  color: #fff;
  display: flex;
  fill: #fff;
  height: 56px;
  justify-content: space-between;
  padding: 0 10px;
`;

const HeaderTitle = styled.h2`
  margin: 0;
  font-size: 16px;
  color: #fff;
`;

const HeaderIcon = styled.a`
  cursor: pointer;
`;

const CloseIcon = () => (
  <svg
    height="24"
    viewBox="0 0 24 24"
    width="24"
    xmlns="http://www.w3.org/2000/svg"
  >
    <path
      d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12
      13.41 17.59 19 19 17.59 13.41 12z"
    />
    <path d="M0 0h24v24H0z" fill="none" />
  </svg>
);

const ChatBotView = () => {
  const [shouldRedirectToMap, setShouldRedirectToMap] = useState(false);

  let userName = null;
  let location = null;
  const locationGeo = null;
  let email = null;
  let preferredLocations = null;

  function onNextStepTrigger(e) {
    let lastStepName = "";
    let previousStepName;
    for (const i in e.steps) {
      previousStepName = lastStepName;
      lastStepName = i;
    }
    analyticsWrapper.logEvent(
      "ChatBotStep" + e.steps[lastStepName].id + "Triggered",
      {
        step: e.steps[lastStepName],
        value: e.value,
      },
    );

    if(lastStepName === "5" && e.value === "No") {
      analyticsWrapper.logEvent("ChatBotSkipClicked", {
        target: "NoAnswer",
      });
      setShouldRedirectToMap(true);
    }

    switch (previousStepName) {
      case "6":
        userName = e.value;
        break;
      case "14":
        preferredLocations = e.value;
        break;
      case "15":
        location = e.value;
        // const result = zipcodes.lookup(location);
        // if (result) {
        //   location = result.city;
        //   locationGeo = result;
        // }
        break;
      case "20":
        email = e.value;
        break;
      default:
        break;
    }

    const nextStepId = parseInt(e.steps[lastStepName].id) + 1 + "";
    return nextStepId;
  }

  function redirectToMap(e) {
    onNextStepTrigger(e);
    setTimeout(function () {
      setShouldRedirectToMap(true);
    }, 6000);
  }

  function onClosedIconClicked() {
    analyticsWrapper.logEvent("ChatBotSkipClicked", {
      target: "ClosedIcon",
    });
    setShouldRedirectToMap(true);
  }

  if (shouldRedirectToMap) {
    return <Redirect to="/" />;
  }

  return (
    <div className={"container"}>
      <Row>
        <Col style={{ margin: "0 auto" }}>
          <ChatBot
            headerTitle={"JoinMeOnHike.com"}
            headerComponent={
              <Header className="rsc-header">
                <HeaderTitle className="rsc-header-title">
                  JoinMeOnHike.com
                </HeaderTitle>
                <HeaderIcon
                  className="rsc-header-close-button"
                  onClick={onClosedIconClicked}
                >
                  <CloseIcon />
                </HeaderIcon>
              </Header>
            }
            steps={[
              {
                id: "1",
                message: "Hi! I'm your personal virtual assistant.",
                trigger: onNextStepTrigger,
              },
              {
                id: "2",
                message:
                  "I was built with help of hundreds of hikers who accomplished thousands of hikes.",
                trigger: onNextStepTrigger,
                delay: 1500,
              },
              {
                id: "3",
                message:
                  "My goal is to help you find your next perfect group hike.",
                trigger: onNextStepTrigger,
                delay: 3500,
              },
              {
                id: "4",
                message: "Do you want to continue?",
                trigger: onNextStepTrigger,
                delay: 2000,
              },
              {
                id: "5",
                options: [
                  {
                    value: "Yes",
                    label: "Yes",
                    trigger: onNextStepTrigger,
                  },
                  {
                    value: "No",
                    label: "No",
                    trigger: onNextStepTrigger,
                  },
                ],
              },
              {
                id: "6",
                message: "What is your name?",
                trigger: onNextStepTrigger,
              },
              {
                id: "7",
                user: true,
                trigger: onNextStepTrigger,
              },
              {
                id: "8",
                message: "Hi {previousValue}, nice to meet you!",
                trigger: onNextStepTrigger,
              },
              {
                id: "9",
                message: "When are you planning to hike?",
                trigger: onNextStepTrigger,
              },
              {
                id: "10",
                options: [
                  {
                    value: "ThisMonth",
                    label: "This month",
                    trigger: onNextStepTrigger,
                  },
                  {
                    value: "NextMonth",
                    label: "Next month",
                    trigger: onNextStepTrigger,
                  },
                  {
                    value: "Later",
                    label: "Later",
                    trigger: onNextStepTrigger,
                  },
                ],
              },
              {
                id: "11",
                message: "Why do you hike?",
                trigger: onNextStepTrigger,
              },
              {
                id: "12",
                user: true,
                trigger: onNextStepTrigger,
              },
              {
                id: "13",
                message: "Where do you like to hike?",
                trigger: onNextStepTrigger,
              },
              {
                id: "14",
                options: [
                  {
                    value: "Forests",
                    label: "Forests",
                    trigger: onNextStepTrigger,
                  },
                  {
                    value: "Mountains",
                    label: "Mountains",
                    trigger: onNextStepTrigger,
                  },
                  {
                    value: "Lakes",
                    label: "Lakes",
                    trigger: onNextStepTrigger,
                  },
                  {
                    value: "Hills",
                    label: "Hills",
                    trigger: onNextStepTrigger,
                  },
                  {
                    value: "Rivers",
                    label: "Rivers",
                    trigger: onNextStepTrigger,
                  },
                  {
                    value: "Beaches",
                    label: "Beaches",
                    trigger: onNextStepTrigger,
                  },
                ],
              },
              {
                id: "15",
                message:
                  "Where do you like to start your hikes, please enter location?",
                trigger: onNextStepTrigger,
              },
              {
                id: "16",
                user: true,
                trigger: onNextStepTrigger,
              },
              {
                id: "17",
                message:
                  "While we are searching, I'll provide you with a quick introduction of our platform.",
                trigger: onNextStepTrigger,
                delay: 2500,
              },
              {
                id: "18",
                component: (
                  <>
                    <Image width={200} src={MapExample} />
                  </>
                ),
                trigger: onNextStepTrigger,
                delay: 3500,
              },
              {
                id: "19",
                message:
                  "On the map we show blue markers with upcoming hiking events. When you click on the marker you will see details of the event.",
                trigger: onNextStepTrigger,
                delay: 4500,
              },
              {
                id: "20",
                message:
                  "We are still searching... Please provide your email for notifications.",
                trigger: onNextStepTrigger,
              },
              {
                id: "21",
                user: true,
                trigger: onNextStepTrigger,
              },
              {
                id: "22",
                message:
                  "Unfortunately, we can't find any hikes for you. We will redirect you to the map for manual exploration.",
                trigger: redirectToMap,
                delay: 4000,
              },
              {
                id: "23",
                message:
                  "In 5 seconds we will redirect you to the map. See you!",
                end: true,
              },
            ]}
          />
        </Col>
      </Row>
    </div>
  );
};

declare let module: Record<string, unknown>;

export default hot(module)(withRouter(ChatBotView));
