// https://developers.amplitude.com/docs/javascript
import amplitude from "amplitude-js";
import { appSettings } from "./appsettings";

amplitude
  .getInstance()
  .init(appSettings.amplitudeKey, { includeReferrer: true, includeUtm: true });
export const analytics = amplitude;

const instance = amplitude.getInstance();
let wrapper = instance;
if (appSettings.mode === "debug") {
  wrapper = {
    logEvent: function (name, o) {
      console.log(name, o);
      instance.logEvent(name, o);
    },
  };
}

export const analyticsWrapper = wrapper;
